import { RxViewDesignerActionModel } from '@helix/platform/view/designer';
import { Injector } from '@angular/core';

import {
  ExpressionFormControlComponent,
  IExpressionFormControlOptions,
  ISelectFormControlOptions,
  SelectFormControlComponent
} from '@helix/platform/shared/components';
import {
  IViewActionDesignPropertyEditorConfig,
  IViewActionDesignSandbox,
  RX_LAUNCH_BEHAVIOR,
  ViewActionDesignEditableProperties
} from '@helix/platform/view/api';
import { ILaunchUrlViewActionParams } from './launch-url-view-action-params.interface';
import { map } from 'lodash';

export class RxLaunchUrlViewActionDesignModel extends RxViewDesignerActionModel {
  static getInitialProperties(
    initialProperties: ViewActionDesignEditableProperties<ILaunchUrlViewActionParams>
  ): ViewActionDesignEditableProperties<ILaunchUrlViewActionParams> {
    return {
      url: null,
      launchBehavior: RX_LAUNCH_BEHAVIOR.newWindow.value,
      ...initialProperties
    };
  }

  constructor(protected injector: Injector, readonly sandbox: IViewActionDesignSandbox<ILaunchUrlViewActionParams>) {
    super(injector, sandbox);
    this.sandbox.setActionPropertyEditorConfig(this.getActionEditorConfig());
  }

  private getActionEditorConfig(): IViewActionDesignPropertyEditorConfig {
    return [
      {
        name: 'url',
        component: ExpressionFormControlComponent,
        options: {
          label: 'URL',
          isRequired: true,
          dataDictionary$: this.expressionConfigurator.getDataDictionary(),
          operators: this.expressionConfigurator.getOperators()
        } as IExpressionFormControlOptions
      },
      {
        name: 'launchBehavior',
        component: SelectFormControlComponent,
        options: {
          label: 'Launch behavior',
          options: map(RX_LAUNCH_BEHAVIOR, (value) => ({
            name: value.content,
            id: value.value
          })),
          sortAlphabetically: false
        } as ISelectFormControlOptions
      }
    ];
  }
}
